<template>
    <div :id="tableId" class="table-wrop">
        <div class="list-wrop">
            <el-table v-if="headerData.length" :data="tableData" class="table" ref="dzsTable" v-loading="loading"
                @selection-change="handleSelectionChange" :stripe="true" v-bind="{ ...tableOptions }"
                :height="tableHeight ? tableHeight : '100%'">
                <el-table-column type="selection" width="55" v-if="showCheckbox"></el-table-column>
                <template v-for="(option, index) in headerData">
                    <template v-if="!option.isSlot">
                        <el-table-column :label="option.label" :width="option.width" :prop="option.key"
                            :class-name="'table-column_' + columnClassName" v-bind="{ ...option.props }"
                            :key="index"></el-table-column>
                    </template>
                    <template v-else>
                        <el-table-column :class-name="'table-column_' + columnClassName" :key="index"
                            :label="option.label" :width="option.width" v-bind="{ ...option.props }">
                            <template slot-scope="scope">
                                <div>
                                    <slot :name="option.key" :row="{ ...scope.row }"></slot>
                                </div>
                            </template>
                        </el-table-column>
                    </template>
                </template>
                <el-table-column v-if="showOperation && operation.length"
                    :class-name="'table-column_' + columnClassName" v-bind="operationOption" label="">
                    <template slot-scope="scope">
                        <el-button type="text" class="btn" size="small" v-for="(item, index) in operation" :key="index">
                            <span @click="onEdit(scope.row)" v-if="item == 'edit'"></span>
                            <span @click="onDetails(scope.row)" v-if="item == 'detail'"></span>
                            <el-popconfirm title="" @confirm="onDel(scope.row)">
                                <template #reference>
                                    <span v-if="item == 'detele'"></span>
                                </template>
                            </el-popconfirm>
                        </el-button>
                        <slot name="button" :row="{ ...scope.row }"></slot>
                    </template>
                </el-table-column>
            </el-table>
            <div class="table-empty" v-if="!headerData.length">
                
            </div>
            <div class="sider-box">
                <dzs-tabel-sider v-if="showSider" :column="tableHeader" :list="tableData"  @change="onSiderChange"
                    @changeHeight="changeHeight" @search="siderSearch" :forceSearch="forceSearch" :show-siders="showSiders"></dzs-tabel-sider>
            </div>
        </div>
        <div class="table-footer" v-if="(total > maxSize) && showPage">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :page-sizes="[maxSize, maxSize * 2, maxSize * 4]"  v-bind="{...paginationSet}" :total="total"></el-pagination>
        </div>
    </div>
</template>

<script>
/**
 * @name 
 * @props operation  edit  details  del 
 * @props tableHeader key  label   key /slotname width  isSlot:slot props:el-table-column
 * @props showCheckbox   false   true 
 * @props maxSize  10
 * @props showPage  true 
 * @props operationOption 
 * @props list 
 * @props total 
 * @props tableOptions ui
 * @props loading   tableOptions element-loading-text="" element-loading-spinner="el-icon-loading"
 * @props height 
 * @props showSider  true 
 * @props showOperation  true 
 * @props forceSearch  false  true 
 *
 * @methods selectionChange  
 * @methods change / /  status:page   status:size 
 * @methods change    status:delete   status:edit  status:details 
 * @methods onEdit 
 * @methods onDetails 
 * @methods onDelete 
 * @methods setSelection 
 * @methods getTableMethods 
 * @methods sizeChange 
 * @methods currentChange 
 * 
 * @slot button 
 *
 */
import dzsTabelSider from "./sider/dzs-tabel-sider.vue";
import defaultProps from "./props";
import deepCopy from "./deepCopy";
export default {
    name: "dzs-table",
    components: {
        dzsTabelSider,
    },
    props: defaultProps,
    data() {
        return {
            paginationSet: {
                background: false,
                layout: "total, sizes, prev, pager, next, jumper",
            },
            headerData: [],
            columnClassName: "" ,
            tableData: [],
            tableId: `dzsTable${new Date().getTime()}`,
            isPhone : false
        };
    },
    watch: {
        tableHeader: {
            handler(val) {
                this.headerData = JSON.parse(JSON.stringify(val));
            },
            deep: true,
            immediate: true,
        },
        list : {
            handler(val){
                this.tableData = deepCopy(val);
            },
            deep : true,
            immediate : true
        }
    },
    computed: {
        /***/
        tableHeight() {
            let height = "";
            if (this.height) {
                if (Number(this.height) != NaN && Number(this.height) > 0) {
                    height = Number(this.height) + "px";
                } else {
                    height = this.height;
                }
            }
            return height;
        },

    },
    mounted() {
        this.listenFormBoxWidth();
    },
    methods: {
        /***/
        handleSizeChange(num) {
            this.onChange("size", '', num);
            this.$emit("sizeChange", num);
        },
        /***/
        handleCurrentChange(num) {
            this.onChange("page", '', num);
            this.$emit("currentChange", num);
        },
        /***/
        onEdit(item) {
            this.onChange("edit", "", item);
            this.$emit("onEdit", item);
        },
        /***/
        onDetails(item) {
            this.onChange("details", "", item);
            this.$emit("onDetails", item);
        },
        /***/
        onDel(item) {
            this.onChange("delete", "", item);
            this.$emit("onDelete", item);
        },
        /***/
        onChange(status, title, data) {
            let obj = { status: status, title: title, data: data, };
            this.$emit("change", obj);
            this.$emit("onChange", obj);
        },
        /***/
        handleSelectionChange(val) {
            this.$emit("selectionChange", val);
        },
        /***/
        setSelection(rows) {
            if (rows) {
                rows.forEach((row) => {
                    this.$refs.dzsTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.dzsTable.clearSelection();
            }
        },
        /***/
        onSiderChange(val) {
            this.headerData = val;
        },
        /***/
        changeHeight(e) {
            this.columnClassName = e;
        },
        /***/
        getTableMethods() {
            return this.$refs.dzsTable;
        },
        /***/
        selectable(row, index) {
            if (this.showCheckbox) {
                return row.selectable === false ? row.selectable : true;
            }
            return true;
        },
        /***/
        siderSearch(list){
            this.tableData = list;
        },
        /**form-box*/
        listenFormBoxWidth() {
            let paginationSet =  {
                background: false,
                layout: "total, sizes, prev, pager, next, jumper",
            }
            let paginationSetPhone =  {
                background: false,
                layout: "sizes , total,  prev, next",
            }
            const resizeObserver = new ResizeObserver(entries => {
                for (let entry of entries) {
                    const { width } = entry.contentRect;
                    this.paginationSet = width <= 768 ? paginationSetPhone : paginationSet;
                }
            })
            resizeObserver.observe(document.querySelector(`#${this.tableId}`));
        }
    },
};
</script>

<style lang="less" scoped>
.table-wrop {
    height: 100%;
    position: relative;

    .list-wrop {
        padding: 0 10px;
        overflow-y: scroll;
        -ms-overflow-style: none;
        /* IE 10+ */
        scrollbar-width: none;

        /* Firefox */
        &::-webkit-scrollbar {
            /* Chrome Safari */
            display: none;
        }

        display: flex;

        :deep(.el-table th, .el-table tr) {
            background-color: var(--ThemeColor01);
            color: #282c33;

            .cell {
                font-weight: normal;
            }
        }

        :deep(.el-table__body-wrapper) {

            // 
            &::-webkit-scrollbar {
                width: 6px;
                background-color: #fff;
            }

            // 
            &::-webkit-scrollbar-track {
                background-color: transparent;
                -webkit-border-radius: 2em;
                -moz-border-radius: 2em;
                border-radius: 2em;
            }

            // 
            &::-webkit-scrollbar-track-piece {
                background-color: transparent;
                -webkit-border-radius: 2em;
                -moz-border-radius: 2em;
                border-radius: 2em;
            }

            // 
            &::-webkit-scrollbar-thumb {
                background-color: #e9e7e7;
                -webkit-border-radius: 2em;
                -moz-border-radius: 2em;
                border-radius: 2em;
            }
        }

        :deep(.el-table__fixed-right-patch) {
            background-color: #f3f8ff;
        }

        :deep(.el-table) {
            tr {
                .table-column_mini {
                    height: 30px;
                    padding: 0;
                }

                .table-column_middle {
                    height: 50px;
                }

                .table-column_max {
                    height: 70px;
                }
            }
        }
    }

    .table {
        border: 1px solid #ebeef5;
    }

    .table-empty {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #ebeef5;
        width: 100%;
    }

    @media screen and (max-width: 768px) {
        .table {
            height: auto;
        }
    }

    .table-footer {
        text-align: right;
        padding-top: 10px;
    }

    @media screen and (max-width: 768px) {
        .table-footer {
            position: relative;
            bottom: 0;
            right: 0;
            z-index: 1;
            margin-top: 20px;
            padding: 20px;
            text-align: right;
            width: 100%;
            overflow-x: auto;
        }
    }

    .operation {
        cursor: pointer;

        >span {
            padding-right: 10px;
            color: var(--ThemeColor);

            span {
                font-size: 14px;
            }

            &:last-child {
                padding-right: 0;
            }
        }
    }

    .empty {
        text-align: center;
        padding-top: 10px;
    }

    .scope-span {
        color: var(--fontColor);
    }

    :deep(.el-tag--dark) {
        border-color: transparent;
        display: inline-block !important;
        padding: 0 10px;
        margin: 0 auto;
    }
}

.btn {
    span {
        color: var(--ThemeColor);
    }

    :deep(span) {
        color: var(--ThemeColor);
    }
}

@media screen and (max-width: 768px) {
    .table-wrop {
        .btn {
            width: 100%;
            margin-left: 0;
        }
    }
}
</style>
